.loader {
  font-size: 14px;
  position: relative;
  height: 100vh;
  width: 100vw;

  &::before {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #c95656;
    // border-bottom: 8px solid #140d0d85;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    animation: spin 2s linear infinite;

    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
    will-change: transform;
    content: "";
  }

  & .loaderContent {
    padding-top: 90px;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
    letter-spacing: 0.1em;
    font-weight: bold;
  }
}

@keyframes spin {
  0% {
    transform: translate3d(-50%, -50%, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(-50%, -50%, 0) rotate(360deg);
  }
}

